import { isFlowDeclaration } from "@babel/types";
import $ from "jquery";

class Notes {
  constructor() {
    this.events();
  }

  events() {
    $("#my-notes").on("click", ".delete-note", this.deleteNote);
    $("#my-notes").on("click", ".edit-note", this.editNote.bind(this));
    $("#my-notes").on("click", ".update-note", this.updateNote.bind(this));
    $(".submit-note").on("click", this.createNote.bind(this));
  }

  deleteNote(e) {
    console.log("notes clicked");
    const thisNote = $(e.target).parents("li");
    $.ajax({
      beforeSend: (xhr) => {
        xhr.setRequestHeader("X-WP-Nonce", universityData.nonce);
      },
      url: `${universityData.root_url}/wp-json/wp/v2/note/${thisNote.data(
        "id"
      )}`,
      type: "DELETE",
      success: (response) => {
        thisNote.slideUp();
        console.log("Note Deleted!");
        console.log(response);
      },
      error: (response) => {
        console.log("Error while deleting note!");
        console.log(response);
      },
    });
  }

  editNote(e) {
    const thisNote = $(e.target).parents("li");
    if (thisNote.data("state") == "editable") {
      this.makeNoteReadOnly(thisNote);
    } else {
      this.makeNoteEditable(thisNote);
    }
  }

  makeNoteEditable(thisNote) {
    thisNote
      .find(".edit-note")
      .html('<i class="bi bi-x" aria-hidden="true"></i>Cancel');
    thisNote
      .find(".note-title-field", ".note-body-field")
      .removeAttr("readonly")
      .addClass("note-active-field");
    thisNote.find(".update-note").addClass("update-note--visible");
    thisNote.data("state", "editable");
  }

  makeNoteReadOnly(thisNote) {
    thisNote
      .find(".edit-note")
      .html('<i class="bi bi-pencil" aria-hidden="true"></i>Edit');
    thisNote
      .find(".note-title-field", ".note-body-field")
      .attr("readonly", "readonly")
      .removeClass("note-active-field");
    thisNote.find(".update-note").removeClass("update-note--visible");
    thisNote.data("state", "cancel");
  }

  updateNote(e) {
    const thisNote = $(e.target).parents("li");
    const ourUpdatedPost = {
      title: thisNote.find(".note-title-field").val(),
      content: thisNote.find(".note-body-field").val(),
    };
    $.ajax({
      beforeSend: (xhr) => {
        xhr.setRequestHeader("X-WP-Nonce", universityData.nonce);
      },
      url: `${universityData.root_url}/wp-json/wp/v2/note/${thisNote.data(
        "id"
      )}`,
      type: "POST",
      data: ourUpdatedPost,
      success: (response) => {
        this.makeNoteReadOnly(thisNote);
        console.log(response);
      },
      error: (response) => {
        console.log("Error while deleting note!");
        console.log(response);
      },
    });
  }

  createNote(e) {
    const newNoteData = {
      title: $(".new-note-title").val(),
      content: $(".new-note-body").val(),
      status: "publish",
    };
    $.ajax({
      beforeSend: (xhr) => {
        xhr.setRequestHeader("X-WP-Nonce", universityData.nonce);
      },
      url: `${universityData.root_url}/wp-json/wp/v2/note/`,
      type: "POST",
      data: newNoteData,
      success: (response) => {
        $(".new-note-title", ".new-note-body").val("");
        $(`
        <li data-id="${response.id}">
                    <input readonly class="note-title-field" value="${response.title.raw}" />
                    <span class="edit-note"><i class="bi bi-pencil" aria-hidden="true"></i>Edit</span>
                    <span class="delete-note"><i class="bi bi-trash" aria-hidden="true"></i>Delete</span>
                    <textarea readonly class="note-body-field">${response.content.raw}</textarea>
                    <span class="update-note btn btn-sm btn-primary"><i class="bi bi-arrow-right" aria-hidden="true"></i>Save</span>
        </li>
        `)
          .prependTo("#my-notes")
          .hide()
          .slideDown();
        console.log(response);
      },
      error: (response) => {
        // if(response.responseText = ){

        // }
        console.log("Error while creating note!");
        console.log(response);
      },
    });
  }
}

export default Notes;
